/*
Root vars. STANDARD FONTS AND COLORS 
*/
*, *::before, *::after {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
}

.main-header {
  height: 9vh;
  background: #202125;
  filter: drop-shadow(0px 4px 2px rgba(0, 0, 0, 0.5));
  grid-column: 1 / span all;
  grid-row: 1 / 2;
  display: flex;
  align-items: center;
}

.main-header .logo {
  object-fit: contain;
  height: 90%;
  margin: 0 1vh;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 9vh 1fr;
  width: 100%;
  height: 100%;
  background: #191A1D;
  color: #E0E0E0;
}

.container__left {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  background: #191A1D;
  width: 100%;
}

.container__right {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  width: 100%;
}

.container__right--top {
  height: calc(50% - 4.5vh);
}

.section-header {
  height: 9vh;
  background: #202125;
  filter: drop-shadow(0px 4px 2px rgba(0, 0, 0, 0.5));
}

.drop-shadow-test {
  font-size: 3rem;
  filter: drop-shadow(0px 0px 10px white);
}

.container__left {
  padding: 10px;
}

.container__left > section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 4.5vh auto;
  gap: 5px;
}

.container__left > section header {
  grid-column: 1/3;
  font-size: 24px;
  background-color: #202125;
}

.container__left > section section {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.container__left > section section svg text {
  font-size: 0.7rem;
}

.container__left > section section svg:nth-of-type(5n+1) > g > path {
  fill: #79A92C;
}

.container__left > section section svg:nth-of-type(5n+2) > g > path {
  fill: #901F93;
}

.container__left > section section svg:nth-of-type(5n+3) > g > path {
  fill: #DC383D;
}

.container__left > section section svg:nth-of-type(5n+0) > g > path {
  fill: #aaa;
}

.container__left > section section svg:nth-of-type(5n+4) > g > path {
  fill: #5979C5;
}
